img, picture {
  display: block;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.list {
  list-style: none;
}

.link, .linki, .js-modal-close {
  text-decoration: none;
  transition: all .3s ease-out;
  display: block;
}

.button, .link, .address, .js-open-modal, .modal__cross, .js-modal-close, .checkbox:hover ~ .item-label, .checkbox:hover {
  cursor: pointer;
  transition: all .3s ease-out;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.arrow-right {
  fill: currentColor;
  margin-left: 7px;
  display: inline-block;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(46, 47, 66, .4);
  transition: all .3s ease-out;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  min-width: 280px;
  opacity: 0;
  visibility: hidden;
  max-width: 100%;
  max-height: 100vh;
  z-index: 500;
  background: var(--text-color-light);
  border: 2px solid var(--default-color);
  border-radius: 10px;
  padding: 28px 20px;
  transition: all .3s ease-out;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
  transition: all .3s ease-out;
}

.js-modal-close {
  width: 24px;
  height: 24px;
  z-index: 1000;
  transition: all .3s ease-out;
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal__cross:hover, .close-white:hover {
  animation-name: closebtn;
  animation-duration: 3s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  animation-direction: normal;
}

.modal__cross:hover {
  fill: currentColor;
  transition: all .3s ease-out;
}

.modal, .overlay .modal.active, .overlay.active {
  overflow-y: scroll;
}

@keyframes changeBgColor {
  0% {
    background-color: var(--section-gren-color);
  }

  50% {
    background-color: var(--default-color);
  }

  100% {
    background-color: var(--add-text-color);
  }
}

@keyframes closebtn {
  0% {
    fill: var(--default-color);
  }

  30% {
    fill: var(--add-text-color);
  }

  50% {
    fill: var(--text-color-dark);
  }

  70% {
    fill: var(--add-text-color);
  }

  100% {
    fill: var(--default-color);
  }
}

@media screen and (max-width: 480px) {
  .modal {
    max-width: 280px;
  }
}

